import {Component, Inject, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {IRepository} from '../../../../../domain/interfaces/irepository.interface';
import {ChangeStatusPayUToken} from '../../../services/plans/changestatuspayu.provider';
import {StatusPayURequest} from '../../../../../domain/models/plans/statuspayurequest.model';

@Component({
  selector: 'app-confirmation',
  templateUrl: './confirmation.component.html',
  styleUrls: ['./confirmation.component.css']
})
export class ConfirmationComponent implements OnInit {

  public requestChangeStatusPayU = new StatusPayURequest();
  public vIdTransactionPublic: string;
  public comprobante: string;
  public telefono: string;
  public correoElectronico: string;
  public totalPagado: string;
  public referenciaPago: string;
  public fechaCreacion: string;
  public banco: string;
  public estadoTransaccion: string;

  public ApiKeypublic: string;
  public merchant_id: string;
  public referenceCode: string;
  public TX_VALUE: string;
  public currency: string;
  public transactionState: string;
  public firma_cadena: string;
  public firmacreada: string;
  public firma: string;
  public reference_pol: string;
  public cus: string;
  public extra1: string;
  public pseBank: string;
  public lapPaymentMethod: string;
  public transactionId: string;

  constructor(@Inject(ChangeStatusPayUToken) private changeStatusPayUService: IRepository<StatusPayURequest>, private router: Router, private activatedRoute: ActivatedRoute) {

  }

  ngOnInit() {
    this.loadParams();
  }

  statusPayU() {
    this.requestChangeStatusPayU.vIdTransactionPublic = this.estadoTransaccion;
    this.requestChangeStatusPayU.vIdEstatusTransaction = this.estadoTransaccion;
    this.requestChangeStatusPayU.vIdPayu = this.estadoTransaccion;
    this.requestChangeStatusPayU.vIdStatusSale = this.estadoTransaccion;

    this.changeStatusPayUService.create(this.requestChangeStatusPayU).subscribe(response => {
      this.router.navigate(['gestorconsulta']);
    });

  }

  private loadParams() {

    this.activatedRoute.queryParams.subscribe(params => {

      this.ApiKeypublic = '4Vj8eK4rloUd272L48hsrarnUA';
      this.merchant_id = params['merchantId'];
      this.referenceCode = params['referenceCode'];
      this.TX_VALUE = params['TX_VALUE'];
      this.currency = params['currency'];
      this.transactionState = params['transactionState'];
      //this.firma_cadena = '$ApiKey~$merchant_id~$referenceCode~$New_value~$currency~$transactionState';
     // this.firmacreada = md5($firma_cadena);
      this.firma = params['signature'];
      this.reference_pol = params['reference_pol'];
      this.cus = params['cus'];
      this.extra1 = params['description'];
      this.pseBank = params['pseBank'];
      this.lapPaymentMethod = params['lapPaymentMethod'];
      this.transactionId = params['transactionId'];


      this.vIdTransactionPublic = (params['vIdTransactionPublic'] != null) ? params['vIdTransactionPublic'] : '';
      this.comprobante = (params['comprobante'] != null) ? params['comprobante'] : '';
      this.telefono = (params['telefono'] != null) ? params['telefono'] : '';
      this.correoElectronico = (params['correoElectronico'] != null) ? params['correoElectronico'] : '';
      this.totalPagado = (params['totalPagado'] != null) ? params['totalPagado'] : '';
      this.referenciaPago = (params['referenciaPago'] != null) ? params['referenciaPago'] : '';
      this.fechaCreacion = (params['fechaCreacion'] != null) ? params['fechaCreacion'] : '';
      this.banco = (params['banco'] != null) ? params['banco'] : '';
      this.estadoTransaccion = (params['estadoTransaccion'] != null) ? params['estadoTransaccion'] : '';
    });

  }


  verHistorial() {
  }

  consultar() {
  }
}
